header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary),transparent);
    width: 20rem;
    height: 28rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 5rem;
    border-radius:2rem 2rem;
    overflow: hidden;
    padding: 3rem 3rem;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/*===============MEDIA QUERIES FOR MEDIUM DEIVCES===============*/
@media screen and (max-width: 1024px) {
    header{
        height:68vh ;
    }

    .me {
        height: 22rem;
    }
}


/*===============MEDIA QUERIES FOR SMALL DEIVCES===============*/
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }

    .header__socials,.scroll__down{
        display: none;
    }

    .me {
        height: 22rem;
    }

}